import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { pointLadderTen } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    gradedCode: '',
    graded: '',
    englishName: '',
    fromScores: '',
    toScores: '',
    sysName: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormPointLadderTen extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const teacherMap = pointLadderTen.filter(teacher => teacher.code === code)[0];
    const initialValue = handleInitialValue(teacherMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Cập nhật xếp loại thang điểm 10' : 'Thêm xếp loại thang điểm 10'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Tên hệ" {...formItemLayout}>
              {getFieldDecorator('sysName', {
                initialValue: initialValue.sysName,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Mã xếp loại" {...formItemLayout}>
              {getFieldDecorator('gradedCode', {
                initialValue: initialValue.gradedCode,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input disabled={teacherId} />)}
            </Form.Item>
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('graded', {
                initialValue: initialValue.graded,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('englishName', {
                initialValue: initialValue.englishName,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromScores', {
                initialValue: initialValue.fromScores,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toScores', {
                initialValue: initialValue.toScores,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#/point-ladder-ten/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPointLadderTen {...props} />
  </Page>
));



